import { StyleSheet } from 'aphrodite';
import { mediumUp, largeUp } from 'style/breakpoints';

export default StyleSheet.create({
    Wrapper: {
        display: 'grid',
        gridGap: '10px',
        gridTemplateColumns: 'repeat(4, 1fr)',
        gridTemplateRows: 'auto repeat(4, 300px)', 
        gridTemplateAreas: `
            "header header header header"
            "p1 p1 p2 p3"
            "p1 p1 p4 p4"
            "p5 p6 p9 p9"
            "p7 p8 p9 p9"
        `,
        '@media (min-width: 1650px)': {
            gridTemplateColumns: 'auto repeat(4, 400px) auto',
            gridTemplateRows: 'auto repeat(4, 400px)', 
            gridTemplateAreas: `
                "header header header header header header"
                "left   p1     p1     p2     p3     right"
                "left   p1     p1     p4     p4     right"
                "left   p5     p6     p9     p9     right"
                "left   p7     p8     p9     p9     right"`
        },
    },
    Header: {
        gridArea: 'header',
        display: 'flex',
        flexWrap: 'wrap',
        width: '100%',
        [largeUp]: { 
            flexWrap: 'nowrap',
        },
    },
    Lazo: {
        display: 'none',
        [mediumUp]: {
            display: 'initial',
        }
    },
    Main: {
        display: 'grid',
    },
    Project1: {
        background: 'grey',
        gridArea: 'p1',
    },
    Project2: {
        background: 'grey',
        gridArea: 'p2',
    },
    Project3: {
        background: 'grey',
        gridArea: 'p3',
    },
    Project4: {
        background: 'grey',
        gridArea: 'p4',
    },
    Project5: {
        background: 'grey',
        gridArea: 'p5',
    },
    Project6: {
        background: 'grey',
        gridArea: 'p6',
    },
    Project7: {
        background: 'grey',
        gridArea: 'p7',
    },
    Project8: {
        background: 'grey',
        gridArea: 'p8',
    },
    Project9: {
        background: 'grey',
        gridArea: 'p9',
    },
});



